$yellow: #FBBA23;
$blue: #65BB5C;
$pink: #2A2F6A;
$orange: #F23B14;

$duration: 700;

@function randomNum($min, $max) {
  $rand: random();
  $randomNum: $min + floor($rand * (($max - $min) + 1));

  @return $randomNum;
}

.confetti {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  transform: rotateX(90deg);
  transform-style: preserve-3d;

  &__item {
    position: absolute;
    width: 6%;
    height: 10%;
    background: $yellow;
    bottom: 0;
    opacity: 1;
    box-shadow: inset 0 0 2px -1px black;

    @for $i from 2 through 101 {
      &:nth-child(#{$i}) {
        left: 2% + ($i - 1) * .9%;
        transform: rotate(#{randomNum(-180, 180)}deg);
        top: #{randomNum(5, 85)} + '%';
        animation: makeItRain $duration * 1ms ease-out forwards;
        animation-delay: #{randomNum(0, $duration * .8) + 12000}ms;
        animation-duration: #{randomNum($duration * .7, $duration * .8)}ms
      }
    }

    &:nth-child(odd) {
      background: $blue;
    }

    &:nth-child(even) {
      z-index: 1;
    }

    &:nth-child(4n-7) {
      background: $pink;
    }
  }
}