.box {
  width: 8rem;
  height: 8rem;
  margin: 3.5rem;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  transition: transform .5s;
  transform-style: preserve-3d;
  perspective: 20rem;

  &__3d {
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
    transition: transform .5s;
    transform: rotateY(20deg) rotateX(-5deg);
  }

  &__side {
    position: absolute;
    width: 100%;
    height: 100%;
    box-shadow: inset 0 0 2px -1px black;

    &-front {
      transform: translateZ(4rem);
    }

    &-left {
      transform: rotateY(90deg) translateZ(4rem);
    }

    &-right {
      transform: rotateY(-90deg) translateZ(4rem);
    }

    &-backside {
      transform: rotateY(180deg) translateZ(4rem);
    }

    &-bottom {
      transform: rotateX(-90deg) translateZ(4rem);
    }

    &-top {
      transform: rotateX(90deg) translateY(-4rem);
    }
  }

  &__header {
    width: 110%;
    height: 15%;
    position: absolute;
    z-index: 1;
    left: -5%;
    top: -5%;
    transform-style: preserve-3d;
    transition: transform .5s;
    transform-origin: 90%;

    &__side {

      &-front {
        &::after {
          content: '';
          position: absolute;
          top: 0;
          right: 20%;
          width: 10%;
          height: 100%;
          background-color: #fd6bc7;
        }
      }

      &-left, &-right {
        background-color: #9423e1;
      }

      &-front, &-backside {
        background-color: #8811dd;
      }

      &-top {
        background-color: #af40fe;
      }
    }
  }

  &__container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    position: absolute;
    transform-style: preserve-3d;
    transition: transform .5s;

    &__side {

      &-front {
        &::after {
          content: '';
          position: absolute;
          top: 0;
          right: 20%;
          width: 10%;
          height: 100%;
          background-color: #ff4c75;
        }
      }

      &-front, &-backside {
        background-color: #dd3e3e;
      }

      &-left, &-right {
        background-color: #ef4848;
      }

      &-bottom {
        background-color: #f47171;
      }
    }
  }

  &__content {
    height: 20%;
    width: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: -1;
    font-size: 3rem;
    font-family: var(--front-family-montserrat-regular);
    color: #fe9eda;
    user-select: none;
    background-color: #f437b8;
    border-radius: .25rem;
    position: absolute;
    animation: 2s forwards;
  }

  &_main & {
    &__header {
      background-color: #fc2c74;

      &::after {
        background-color: #fb80da;
      }
    }

    &__container {
      background-color: #9211ea;

      &::after {
        background-color: #ff4cdc;
      }
    }
  }

  &:hover:not(&_active):not(&_end) & {
    &__header {
      transform: rotateZ(10deg) rotateX(10deg);
    }
  }

  &_active & {

    &__header {
      animation: open-header 3s forwards;
    }

    &__3d {
      animation: open-container 3s forwards;
    }

    &__container {
      transform: none;
    }
  }

  &_end & {

    &__header {
      animation: close-header 4s forwards;
    }

    &__3d {
      animation: close-container 4s forwards;
    }

    &__container {
      transform: none;
    }
  }
}

@keyframes open-container {
  0% {
    transform: rotateY(20deg) rotateX(-5deg);
  }
  20% {
    transform: rotateY(20deg) rotateX(-5deg);
  }
  30% {
    transform: rotateY(20deg) rotateX(-5deg);
  }
  100% {
    transform: rotateX(-95deg);
  }
}

@keyframes close-container {
  0% {
    transform: rotateX(-95deg);
  }
  25% {
    transform: rotateX(-95deg);
  }
  70% {
    transform: rotateY(20deg) rotateX(-5deg);
  }
  80% {
    transform: rotateY(20deg) rotateX(-5deg);
  }
  100% {
    transform: rotateY(20deg) rotateX(-5deg);
  }
}

@media (max-width: 450px), (max-height: 450px) {
  .box__content {
    font-size: .6rem;
  }
}